.Button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                        supported by Chrome and Opera */
  font-size: 18px;
  text-decoration: underline;
  letter-spacing: 0.5px; }

.empty:hover {
  font-weight: bold; }

.empty:active {
  padding-bottom: 2px; }

.border {
  background: #ffffff;
  outline: 1px solid #353b488f; }

.border:hover {
  outline: 2px solid #353b488f; }

.border:active {
  background: #353b488f; }

.raised {
  -webkit-box-shadow: rgba(0, 0, 0, 0.19) 0px 2.5px 5px 0px, rgba(0, 0, 0, 0.23) -2px 1px 6px 0px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 2.5px 5px 0px, rgba(0, 0, 0, 0.23) -2px 1px 6px 0px; }

.raised:hover {
  -webkit-box-shadow: rgba(0, 0, 0, 0.19) 0px 4px 5px 0px, rgba(0, 0, 0, 0.23) -3px 1px 6px 0px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 4px 5px 0px, rgba(0, 0, 0, 0.23) -3px 1px 6px 0px; }

.raised:active {
  -webkit-box-shadow: rgba(0, 0, 0, 0.19) 0px 2px 3px 0px, rgba(0, 0, 0, 0.23) -1px 1px 4px 0px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 2px 3px 0px, rgba(0, 0, 0, 0.23) -1px 1px 4px 0px; }



