@import url("https://fonts.googleapis.com/css?family=Lobster");
/*Default full viewport application attributes.*/
html,
body,
#root {
  height: 100%;
  margin: 0;
  font-family: "Lobster", sans-serif; }

#App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  min-height: 100%;
  background: #2C3A47;
  color: #2C3A47; }

#title {
  margin-top: -40px;
  font-size: 36px;
  color: #fff;
  letter-spacing: 2px; }

#Buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between; }

.button {
  margin: 15px; }

@media (min-width: 600px) {
  .button {
    margin: 20px; } }



